.dashbowrd{
    background-color: #012939;
    padding: 10px 0px 2rem 10px;
    color: white;
    margin: 10px;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    gap: 3rem;
    max-width: 212px;
    max-height: 901px;
    border: 1px solid white;
}
 .button{
    position: fixed;
    top: 5px;
    z-index: 100;
    left: 10%;
    font-size: 30px;
    font-weight: bold;
    display: none;
    color: var(--secondary-color);
}
.button>:nth-child(1),
.button>:nth-child(2){
    margin-left: 5px;
    cursor: pointer;
}
.dashbowrd .header-dash img{
   width:200px ;
   height: 80px;
}

.dashbowrd .links ul {
    display: flex;
    flex-direction: column;
    gap: 0px;
}
.dashbowrd .links ul li,
.dashbowrd .footer-dash ul li{
    display: flex;
    gap: 1rem;
    align-items: center;
    font-weight: bold;
    font-size: 1rem;
    margin: 0px auto;
    padding: 10px;
    border-radius: 12px 0px 0px 12px;
    transition: .3s;
}
.dashbowrd .links ul li:hover,
.dashbowrd .footer-dash ul li:hover{
    background-color: var(--secondary-color);
}
.dashbowrd .links ul li>:nth-child(1){
    width: 1rem;
    height: 1rem;
}
.dashbowrd .footer-dash ul{
    display: flex;
    flex-direction: column;
    gap: 0px;
}
/* ============== media ============ */
@media screen and (max-width:769px) {
    .dashbowrd{
        position: absolute;
        left: -100rem;
        top: 30px;
        z-index: 100;
}
.dashbowrdOpen{
    left: 0;
}
.button{
    display: block;
}
}
