.login{
    position: absolute;
    top: 50%;
    left: 60%;
    transform: translate(-50%, -50%);
    padding: 3rem;
    background-color: white;
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    gap: 1rem;
}
.login>:nth-child(1){
width: 100%;
}
.login>:nth-child(2){
    color: #012939;
    text-transform: uppercase;
}
.login>:nth-child(3),
.login>:nth-child(4){
     border: none;
    border-bottom: 1px solid #01080a;
    padding: 10px 10px 10px 0px;
    outline: none;
    padding-left: 5px;
    color: #012939;
}
.login>:nth-child(3)::placeholder,
.login>:nth-child(4)::placeholder{
    color:#012939;
    font-weight: bold;
    
}
.login>:nth-child(5){
        color: var(--secondary-color);
    font-weight: bold;
}
.login>:nth-child(6),
.login>:nth-child(7){
    padding: 1rem;
    border: none;
    font-weight: bold;
    font-size: 1.5rem;
}
.login>:nth-child(6){
    background-color: #012939;
    color: white;
}
.login>:nth-child(7){
    background-color: white;
    color:#012939;
    border: 2px solid #012939;
}
.login>:nth-child(8){
    display: flex;
    align-items: center;
    gap: 10px;
    font-weight: bold;
}
.login>:nth-child(8)>:nth-child(1){
    color: #012939;
}
.login>:nth-child(8)>:nth-child(2){
    color: var(--secondary-color);
}
/* ============= media ============= */
@media screen and (max-width:769px) {
.login {
    top: 140px;
    width: 70%;
    transform: translate(0px, 0px);
    left: 2%;
}
}